<template>
  <div id="index">
    <div>
      <router-link :to="{path:'login'}">
        <span class="loginButton">登录/注册</span>
      </router-link>
      <h1>矿大二手</h1>
      
      <div class="buttonWrapper">
        <router-link :to="{path:'sale'}">
        <button class="indexButton">二手</button>
        </router-link>
        <router-link :to="{path:'buy'}">
        <button class="indexButton">求购</button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      "loginActive":false
    }
  },
  methods:{
    "showLogin":function(){
      this.loginActive=true;
    }
  }
}

</script>

<style scoped>
  h1{
    font-size: 70px;
    font-weight: 800;
  }
  p{
    display: inline-block;
    width: 600px;

  }
  #index{
    width: 100%;
    height: 100%;
    min-height: 1200px;
    background-image:url(buy.jpg);
    background-repeat: repeat-y;
    background-size: 100%;
  }
  .indexButton{
    width: 500px;
    height: 78px;
    margin: 10px;
    display: inline-block;
    font-family: 'STSongti-SC-Bold', 'Songti SC Bold', 'Songti SC';
    font-weight: 500;
    font-style: normal;
    font-size: 32px;
    color: rgba(255, 255, 255, 0.929411764705882);
    background-color: rgba(253, 208, 72, 0.847058823529412);
    border: none;
    border-radius: 5px;
  }
  .buttonWrapper{
    width: 700px;
    text-align: center;
    margin: 200px auto;
    animation:mymove 1s;
  }
  @keyframes mymove
  {
  from {margin: 600px auto;}
  to {margin: 200px auto;}
  }
  .loginMenu:hover{
    cursor: pointer;
  }
  
  .indexButton:hover{
    cursor: pointer;
  }
  .loginWrapper{
    position: absolute;
    left: 280px;
  }
  .loginButton{
    display: inline-block;
    margin: 50px;
    text-indent: 600px;
    font-size: 23px;
    font-weight: 800;
    color: rgba(253, 208, 72,1);
  }
</style>


